import validations from '@/mixins/validations'
export default {
  mixins: [validations],
  data() {
    return {
      user: {
        email: '',
        password: ''
      },
      showPassword: false
    }
  },
  methods: {
    async login() {
      try {
        this.$refs.form.validate()
        if (this.valid) {
          this.$store.commit('alert/REMOVE_ALERT')
          this.$store.commit('loading/SET_LOADING', {
            option: 'auth.login',
            value: true
          })
          await this.$auth.loginWith('local', {
            data: this.user
          })
          this.$store.commit('loading/SET_LOADING', {
            option: 'auth.login',
            value: false
          })
        }
      } catch (e) {
        const alert = {
          type: 'error',
          display: 'login-page',
          show: true,
          text: e.response ? e.response.data.message : e.message
        }
        this.$store.commit('alert/SET_ALERT', alert)
        this.$store.commit('loading/SET_LOADING', {
          option: 'auth.login',
          value: false
        })
      }
    }
  }
}
